<template>
<layout>
    <div class="my-order">
        <cube-tab-bar class="tab-nav" v-model="selectedLabel"
            show-slider
            :use-transition="disabled"
            ref="tabNav"
            @click="_clickHandler"
            :data="tabLabels">
       </cube-tab-bar>
       <div class="tab-slide-container">
           <cube-scroll>
                <ul class="list-wrapper">
                    <li v-for="(item, index) in followersData" class="list-item" :key="index">
                        <list :item="item" :index="index" @del="_del"></list>
                    </li>
                </ul>
           </cube-scroll>
       </div>
    </div>
</layout>
</template>
<script>
import { getOrderInfomations } from "@/api/order.js";
import list from './component/list';
export default {
    name: 'my-order',
    data() {
        return {
            selectedLabel: 'all',
            disabled: true,
            tabLabels: [
                {
                    label: '全部',
                    value: 'all',
                },
                {
                    label: '待付款',
                    value: '1',
                },
                {
                    label: '待取货',
                    value: '2',
                },
                // {
                //     label: '待分拣',
                //     value: '3',
                // },
                {
                    label: '清洗中',
                    value: '4',
                },
                {
                    label: '待收货',
                    value: '5',
                },
                // {
                //     label: '待评价',
                //     value: '6',
                // },
                {
                    label: '已完成',
                    value: '7',
                },
                // {
                //     label: '已取消',
                //     value: '8',
                // }
            ],
            followersData: [],
        }
    },
    components: {
        list,
    },
    created() {
        this._getOrderInfomations('all');
    },
    methods: {
        _clickHandler(value) {
            this.followersData = [];
            this._getOrderInfomations(value);
        },
        _getOrderInfomations(orderState) {
            let params = {
                userId: 1,
            }
            if (orderState !== 'all') {
                params.orderState = orderState;
            }
            const toast = this.$createToast({
                time: 5000,
            })
            toast.show()
            getOrderInfomations(params).then(res => {
                this.followersData = res;
                toast.hide();
            })
        },
        _del(id, index) {
            this.followersData.splice(index, 1);
        }
    },
}
</script>
<style lang="stylus" scoped>
.my-order {
    height: 100%;
    .tab-nav {
        height: 80px;
        background-color: #ffffff;
        .cube-tab {
            font-size: 12px;
        }
    }
    .tab-slide-container {
        height: calc(100vh - 178px);
        .list-wrapper {
            padding: 0 12px;
            .list-item {
                background-color: #ffffff;
                margin-top: 12px;
                // box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);
                border-radius: 24px;
            }
        }
    }
}
</style>
            


