<template>
    <div class="item">
        <div class="hedaer">
            <div class="left">下单时间：{{item.startOrderTime | _dateFormat}}</div>
            <div class="right">
                <i class="icon iconfont icon-shanchu" @click="_del"></i>
            </div>
        </div>
        <div class="products">
            <div class="wrap">
                <cube-scroll direction="horizontal">
                    <ul class="product">
                        <li v-for="(x, index) in item.orderDetailWxVOList" :key="index">
                            <img :src="`/picture/${x.img}`" alt="">
                        </li>
                    </ul>
                </cube-scroll>
            </div>
            <div class="total">
                <p>￥{{item.orderMonery}}</p>
                <p>共{{item.goodsNum}}件</p>
            </div>
        </div>
        <div class="btns">
            <div class="status">{{status[item.orderState]}}</div>
            <div class="btn m-order-btn" @click="_go(item)">查看详情</div>      
        </div>
    </div>
</template>
<script>
import { dateFormat } from "@/utils/util.js";
import { deleteOrder } from "@/api/order.js";
export default {
    data() {
        return {
            status: {
                1: '待付款',
                2: '待取货',
                3: '待分拣',
                4: '清洗中',
                5: '待收货',
                6: '待评价',
                7: '已完成',
                8: '已取消',
            },
        };
    },
    props: {
        item: {
            type: Object,
            default() {
                return {};
            }
        },
        index: {
            type: Number,
            default() {
                return 0;
            }
        }
    },
    filters: {
        _dateFormat: function (value) {
            let time = dateFormat(new Date(value), 'yyyy-MM-dd hh:mm:ss');
            return time;
        }
    },
    methods: {
        _go(item) {
            this.$router.push({ path: '/order-detail', query: {id: item.orderId}});
        },
        _del() {
            this.$createDialog({
                type: 'confirm',
                icon: 'cubeic-alert',
                title: '删除订单',
                content: '是否删除订单',
                confirmBtn: {
                    text: '确定',
                    active: true,
                    disabled: false,
                    href: 'javascript:;'
                },
                cancelBtn: {
                    text: '取消',
                    active: false,
                    disabled: false,
                    href: 'javascript:;'
                },
                onConfirm: () => {
                    this._deleteOrder();
                },
                onCancel: () => {
                }
            }).show()
        },
        _deleteOrder() {
            let params = {
                orderId: this.item.orderId,
            }
            const toast = this.$createToast({
                time: 5000,
            })
            toast.show();
            deleteOrder(params).then(res => {
                this.$emit('del', this.item.orderId, this.index);
                toast.hide();
            })
        },
    }
}
</script>
<style lang="stylus" scoped>
.item {
    padding: 24px;
    .hedaer {
        display: flex;
        align-items: center;
        justify-content space-between;
        font-size: 28px;
    }
    .products {
        display: flex;
        align-items: center;
        justify-content space-between;
        font-size: 24px;
        padding: 24px 0;
        .wrap {
            width: 550px;
            .product {
                width: 550px;
                height: 100px;
                li {
                    display: inline-block;
                    width: 100px;
                    height: 100px;
                    border-radius: 12px;
                    margin-right: 24px;
                    img {
                        width: 100px;
                        height: 100px;
                        border-radius: 12px;
                    }
                }    
            }
        }
        .total {
            text-align: center;
        }
    }
    .btns {
        text-align: right;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .status {
            font-size: 24px;
        }
        .btn {
            margin-left: 12px;
        }
    }
}
</style>


